import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, ViewController } from 'ionic-angular';
import { Welcome } from '../welcome/welcome';
import { ToastService } from '../../providers/util/toast.service';

@IonicPage()
@Component({
  selector: 'page-borrowmanage',
  templateUrl: 'borrowmanage.html'
})
export class BorrowManagePage {
  private show :boolean = false;
  private index : number = 0;
  private seleFont :string;
  private statusList :any;
  private tab :boolean;
  private statusL:string = 'ALL';//请求参数
  private pageNum: number = 1;//分页
  private items :any = [
    {
      value:'全部'
    },
    {
      value:'待审核'
    },
    {
      value:'待付款'
    },
    {
      value:'已借款'
    },
    {
      value:'已还款'
    },
    {
      value:'待解冻'
    }
  ];
  private hideBtn :boolean = false;
  constructor(public navCtrl: NavController, public navParams: NavParams,public viewCtrl: ViewController,public http:ToastService) {
    this.tab = navParams.get('tab');
  }

  ionViewDidLoad() {
    this.http.get('phoneApplylend/v1/listAll',{lendPhoneSelectStatus:this.statusL,page:this.pageNum})
    .then(res => {
      this.statusList = res.data;
    });
    if(this.tab){
      this.viewCtrl.showBackButton(false);
    }
  }
  selectFilter(i:number,val:string) :any {
    this.show = !this.show;
    this.index = i;
    this.seleFont = val;
    if (val && val.trim() != '') {
      this.statusList = this.statusList.filter((item) => { 
        if(val == '全部')return item;
        if(item.currentStatus == val){
          return item;
        }
      })
    }
  }
  toPage() {
    this.navCtrl.push('ApplyborrowmoneyPage');
  }
   
  goDetail(value:string,id:string) {
    let name :string;
    switch(value){
        case 'WAITAUDIT':
            name = 'WAITAUDIT'
            break;
        case 'WAITPAY'://待付款
            name = 'WAITPAY'
            break;
        case 'WAITRETURN'://待还款
            name = 'WAITRETURNCHECK'
            break;
        case 'HASRETURN'://已还款
            name = 'RETURNCHECK'
            break;
        case 'HASLEND'://已借款
            name = 'SURERECEIVE'
            break;
        case 'WAITTHAW'://待解冻
            name = 'WAITTHAW'
            break;
        }
    this.navCtrl.push('BorrowDetailPage',{value:value,name:name,id:id});
  }
  dismiss() {
    this.navCtrl.push(Welcome);
  }
  //下拉刷新
  doRefresh(refresher) {
    setTimeout(() => {
      for( let i = 0,len = 5,value;i < len;i++){
        var o = {
          nameWipe:'王博文的借款',
          wipeTime:'借款发生时间：2017-08-23',
          date:'2017-08-31',
          currentStatus:'待付款'
        }
        this.statusList.unshift(o);
      }
      refresher.complete();
    }, 2000);
  }
  //上拉加载
  doInfinite(infiniteScroll) {
    setTimeout(() => {
      this.pageNum++;
      this.http.get('phoneApplylend/v1/listAll',{lendPhoneSelectStatus:this.statusL,page:this.pageNum})
      .then(res => {
        let data = res.data;
        if(res.code == 0){
          for( let i = 0,len = data.length,val;i < len;i++){
            val = data[i];
            this.statusList.push(val);
          }
          if(data.length < 10){//当当前没有更多数据了，下次不再有下拉操作
            infiniteScroll.enable(false);
          }
        }
        infiniteScroll.complete();
      });
      //infiniteScroll.enable(value) value = false 停止下拉操作
    },500);
  }
}



